import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { Portfolio3dComponent } from './portfolio3d/portfolio3d.component';
import { Portfolio2dComponent } from './portfolio2d/portfolio2d.component';
import { PortfolioMusicComponent } from './portfolio-music/portfolio-music.component';
import { PortfolioDigitalComponent } from './portfolio-digital/portfolio-digital.component';

const routes: Routes = [  
  { path: 'digital', component: PortfolioDigitalComponent },
  { path: 'music', component: PortfolioMusicComponent },
  { path: '2d', component: Portfolio2dComponent },
  { path: '3d', component: Portfolio3dComponent },
  { path: '', component: HomeComponent },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
